@import "styles/colors";

.title {
  position: relative;
  color: #FFFFFF;
  margin: 0 0 25px;
  z-index: 1;

  > .--underline {
    position: absolute;
    bottom: 3px;
    left: 50%;
    transform: translateX(-50%);
    display: block;
    content: ' ';
    width: calc(100% + 25px);
    height: 14px;
    border-radius: 7px;
    background-color: $palette-4;
    z-index: -1; } }
