@import "styles/colors";

.menu {
  .menu--link, .menu--link:visited {
    display: inline-block;
    text-transform: uppercase;
    text-decoration: none;
    font-weight: 700;
    color: $text-color-1;
    transition: color 200ms;

    &:hover {
      color: $palette-4; }

    &:not(:last-child) {
      margin-right: 100px; } } }
