@import "styles/colors";

.footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100px;
  padding: 0 5vw;
  background-color: $palette-2;

  .footer--copyright {
    color: $text-color-1;
    font-size: 12px; }

  .footer--icons {
    a:not(:last-child) {
      margin-right: 20px; }

    svg {
      fill: $text-color-1; } } }
