@import "styles/colors";

.btn {
  height: 55px;
  padding: 0 50px;
  outline: none;
  border: none;
  border-radius: 15px;
  background-color: $palette-4;
  color: $text-color-1;
  font-size: 17px;
  font-weight: 700;
  box-shadow: 0 3px 7px 0 rgba(0, 0, 0, 0.5);
  cursor: pointer;
  transition: background-color 200ms, color 200ms;
  text-align: center;
  text-decoration: none;
  line-height: 55px;

  &:hover, &.--active {
    background-color: $text-color-1;
    color: $palette-4; }

  &:active {
    transform: scale(1.015); }

  &.btn--alternative {
    background-color: $palette-3;
    color: $text-color-1; }

  &:hover, &.--active {
    background-color: $text-color-1;
    color: $palette-3; } }
