@import "styles/colors";

.container {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 500px;
  margin: 0 5vw;
  padding: 30px;
  border-radius: 30px;
  background-color: $palette-1;

  &.--largePadding {
    padding: 30px 15vw; }

  &.--tabs {
    padding-top: 50px; }

  &.--coverered {
    padding-top: 625px; }

  .container--tabs {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);

    button:not(:last-child) {
      margin-right: 20px; } }

  .container--cover {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    content: ' ';
    width: 100%;
    height: 600px;
    border-radius: 30px 30px 0 0;
    background-size: cover;
    background-position: center 0; } }
