@import "styles/colors";

$xMargin: 30pt;
$yMargin: 15pt;

.show-list {
  display: flex;

  .show-list--container {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    margin: -$yMargin (-$xMargin); }

  &:not(:first-child) {
    margin-top: 50pt; } }

.show-card {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 200pt;
  height: 200pt;
  border-radius: 15px;
  margin: $yMargin $xMargin;
  background-color: $palette-2;

  .show-card--cover {
    content: ' ';
    width: 100%;
    height: 75%;
    background-position: center center;
    background-size: cover;
    border-radius: 15px 15px 0 0; }

  .show-card--title {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 90%;
    margin: 0 auto;
    color: $text-color-1;
    text-align: center;
    text-transform: uppercase;
    font-size: 0.95rem;
    font-weight: 700; }

  .show-card--blank {
    content: ' ';
    width: 100%;
    height: 15px; }

  .show-card--button {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%) translateY(50%);
    height: 30px;
    line-height: 30px;
    font-size: 0.9rem;
    border-radius: 5pt; } }
